.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.texto_centrado {
  text-align: center;
}

.texto_justificado {
  text-align: justify;
}

.texto_derecha {
  text-align: right;
}

.texto_izquierda {
  text-align: left;
}

.mayuscula {
  text-transform: capitalize;
}

.no-visible {
  display: none;
}

/** Media Queries **/
body {
  background-color: white;
}

#app {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  margin: auto;
}

.trabajo {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
}

h1, h2 {
  /* color: green; */
}

button {
  padding: 1rem 1rem;
}

ul li {
  color: black;
}

ul {
  list-style-type: decimal;
}

.fruta {
  margin-bottom: 2rem;
}

.Mouse {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  margin-top: 2rem;
}
.Mouse .subtotal {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.Mouse .Calcu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
.Mouse h3 {
  display: flex;
  justify-content: center;
  color: brown;
}
.Mouse .reset {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.Post {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
}
.Post .botones {
  display: flex;
  flex-direction: row;
  grid-gap: 0.7rem;
}
.Post .boton-Post {
  margin: auto;
  width: 100%;
}
.Post .boton-blo {
  background-color: rgb(229, 229, 229) !important;
  pointer-events: none;
}

.caja-1 {
  margin: auto;
}
.caja-1 .caja-body {
  display: flex;
  flex-direction: column;
  border: 2px solid red;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
}
.caja-1 .card-title {
  margin: 0 auto 1rem;
  font-family: serif;
  font-size: 1rem;
  color: brown;
}
.caja-1 p {
  font-family: "Times New Roman", Verdana, serif;
  font-size: 0.7rem;
  margin: 0 auto;
}
.caja-1 button {
  margin: 1rem auto 0.5rem;
  border-radius: 10rem;
  padding: 0.8rem;
  font-size: 0.7rem;
  background-color: white;
  font-weight: bold;
}
.caja-1 button:hover {
  background-color: #37C0FF;
  color: white;
}

.prueba {
  padding: 0.5rem;
}
.prueba .lista {
  display: flex;
  flex-direction: row;
  grid-gap: 0.8rem;
  margin: auto;
  height: 70%;
  transform: scale(1);
  width: 90%;
  overflow-x: scroll;
  margin-bottom: 1rem;
  /* position: fixed; */
}

#app {
  /* grid-gap: 0; */
}
#app h1 {
  margin: auto;
}
#app .NamePoke {
  margin: 0;
  margin-left: 1rem;
  /* counter-reset: item 12; */
  list-style: none;
  /* Incrementa el contador para cada elemento de la lista */
  /* Muestra el valor del contador en el contenido del elemento li */
}
#app .NamePoke li {
  counter-increment: item;
}
#app .NamePoke li::before {
  content: counter(item);
  margin-right: 5px;
  font-weight: bold;
}

.Pokemon {
  display: flex;
  flex-direction: column;
}
.Pokemon .nombre {
  font-size: 1.5rem;
  margin: 0 auto;
  color: #66BF9D;
}

.buttonPoke {
  display: flex;
  flex-direction: row;
  /* margin: 0 auto 1rem; */
  grid-gap: 1rem;
  /* height: 1rem; */
}
.buttonPoke .boton {
  margin: auto;
  width: 100%;
}

header {
  line-height: 1.5;
  max-height: 100vh;
}

.logo {
  display: block;
  margin: 0 auto 2rem;
}

nav {
  width: 100%;
  font-size: 12px;
  text-align: center;
  margin-top: 2rem;
}

nav a.router-link-exact-active {
  color: var(--color-text);
}

nav a.router-link-exact-active:hover {
  background-color: transparent;
}

nav a {
  display: inline-block;
  padding: 0 1rem;
  border-left: 1px solid var(--color-border);
}

nav a:first-of-type {
  border: 0;
}

@media (min-width: 1024px) {
  header {
    display: flex;
    place-items: center;
    padding-right: calc(var(--section-gap) / 2);
  }
  .logo {
    margin: 0 2rem 0 0;
  }
  header .wrapper {
    display: flex;
    place-items: flex-start;
    flex-wrap: wrap;
  }
  nav {
    text-align: left;
    margin-left: -1rem;
    font-size: 1rem;
    padding: 1rem 0;
    margin-top: 1rem;
  }
}
.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  margin: auto;
}

.loading {
  margin: auto;
  color: green;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid blue;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: green transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}