#app {
	/* grid-gap: 0; */
	h1 {
		margin: auto;
	}
	.NamePoke {
		margin: 0;
		margin-left: 1rem;
		/* counter-reset: item 12; */
  		list-style: none;

  		/* Incrementa el contador para cada elemento de la lista */
		li {
			  counter-increment: item;
		}

		/* Muestra el valor del contador en el contenido del elemento li */
		li::before {
			  content: counter(item);
			  margin-right: 5px;
			  font-weight: bold;
		}
	}
}

.Pokemon {
	display: flex;
	flex-direction: column;

	.nombre {
		font-size: 1.5rem;
		margin: 0 auto;
		color: #66BF9D;
	}
}

.buttonPoke {
  display: flex;
  flex-direction: row;
  /* margin: 0 auto 1rem; */
  grid-gap: 1rem;
  /* height: 1rem; */

  .boton {
  	
  	margin: auto;
  	width: 100%;
  }
}